.container-error {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #262626;
}

.container-error .img-notfound {
    max-width: 300px;
    margin: 24px 0;
    animation: shake 1s;
}

@keyframes shake {
    10%, 90% {
        transform: translate3d(-1px, 0, 0)
    }
    20%, 90% {
        transform: translate3d(2px, 0,0)
    }

    30%, 50%, 70% {
        transform: translate3d(-4px, 0,0)
    }
    40%, 60% {
        transform: translate3d(4px, 0,0)
    }
}

.container-error h1 {
    color: #fff;
}

.container-error .btn-return-home {
    color: #132742;
    background-color: #fff;
    text-decoration: none;
    padding: 12px 18px;
    border-radius: 4px;
    margin: 14px 0;
    font-weight: bold;
}